import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Layout from './components/Layout';
import { ResourceProvider } from './context/ResourceContext';
import { AuthProvider } from './context/AuthContext';
import StatusGeneralStatus from './pages/StatusGeneralStatus';
import StatusBulkJob from './pages/StatusBulkJob';
import StatusIndex from './pages/IndexStatus';
import IndexReindex from './pages/IndexReindex';
import IndexRemoveResource from './pages/IndexRemoveResource';
import OrgConfigReadConfig from './pages/OrgConfigReadConfig';
import OrgConfigListUsers from './pages/OrgConfigListUsers';
import SiteConfigListConfig from './pages/SiteConfigListSites';
import SiteConfigReadConfig from './pages/SiteConfigReadConfig';
import SiteConfigUpdateSiteConfig from './pages/SiteConfigUpdateConfig';
import PreviewStatus from './pages/PreviewStatus';
import PreviewUpdate from './pages/PreviewUpdate';
import PreviewDelete from './pages/PreviewDelete';
import PreviewBulkJob from './pages/PreviewBulkJob';
import PublishStatus from './pages/PublishStatus';
import PublishResource from './pages/PublishResource';
import PublishUnpublish from './pages/PublishUnpublish';
import PublishBulkJob from './pages/PublishBulkJob';
import CodeStatus from './pages/CodeStatus';
import CodeUpdate from './pages/CodeUpdate';
import CodeDelete from './pages/CodeDelete';
import CachePurgeLive from './pages/CachePurgeLive';

const theme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
});

const App: React.FC = () => {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AuthProvider>
        <ResourceProvider>
          <Router>
            <Layout>
              <Routes>
                <Route path="/" element={<Navigate to="/admin-ui/status/general" replace />} />
                <Route path="/admin-ui/status/general" element={<StatusGeneralStatus />} />
                <Route path="/admin-ui/status/bulk" element={<StatusBulkJob />} />
                <Route path="/admin-ui/index/status" element={<StatusIndex />} />
                <Route path="/admin-ui/index/reindex" element={<IndexReindex />} />
                <Route path="/admin-ui/index/remove" element={<IndexRemoveResource />} />
                <Route path="/admin-ui/org-config/read" element={<OrgConfigReadConfig />} />
                <Route path="/admin-ui/org-config/users" element={<OrgConfigListUsers />} />
                <Route path="/admin-ui/site-config/list" element={<SiteConfigListConfig />} />
                <Route path="/admin-ui/site-config/read" element={<SiteConfigReadConfig />} />
                <Route path="/admin-ui/site-config/update" element={<SiteConfigUpdateSiteConfig />} />
                <Route path="/admin-ui/preview/status" element={<PreviewStatus />} />
                <Route path="/admin-ui/preview/update" element={<PreviewUpdate />} />
                <Route path="/admin-ui/preview/delete" element={<PreviewDelete />} />
                <Route path="/admin-ui/preview/bulk" element={<PreviewBulkJob />} />
                <Route path="/admin-ui/publish/status" element={<PublishStatus />} />
                <Route path="/admin-ui/publish/resource" element={<PublishResource />} />
                <Route path="/admin-ui/publish/unpublish" element={<PublishUnpublish />} />
                <Route path="/admin-ui/publish/bulk" element={<PublishBulkJob />} />
                <Route path="/admin-ui/code/status" element={<CodeStatus />} />
                <Route path="/admin-ui/code/update" element={<CodeUpdate />} />
                <Route path="/admin-ui/code/delete" element={<CodeDelete />} />
                <Route path="/admin-ui/cache/purge" element={<CachePurgeLive />} />
              </Routes>
            </Layout>
          </Router>
        </ResourceProvider>
      </AuthProvider>
    </ThemeProvider>
  );
};

export default App;
